import { Loader } from "@pixi/loaders";
import { sounds } from "../core/SoundManager";
import { sendError } from "..";
import { BaseGame, stateManager, unusedBodies, world } from "../core/BaseGame";
import { finishLoading, startLoading } from "../core/natives/SocialManager";
import { MenuState } from "./states/MenuState";
import { GameViewState } from "./states/GameViewState";
import { Body } from "p2";
import { CircleBlock } from "./states/game/CircleBlock";

export let increaseBlocks: { block: CircleBlock; steps: number }[] = [];
export let explodePoints: [number, number][] = [];
export default class Game extends BaseGame {
  constructor() {
    super(756, 1344);
  }
  protected createStates() {
    stateManager.add(new MenuState());
    stateManager.add(new GameViewState());
    stateManager.start(MenuState);
  }
  protected initContactListener() {
    world.on("endContact", (evt) => {
      if (!evt.shapeA.data || !evt.shapeB.data) return;
      const bodyA: Body = evt.bodyA;
      const bodyB: Body = evt.bodyB;
      const instanceA = (bodyA as any).userData as CircleBlock;
      const instanceB = (bodyB as any).userData as CircleBlock;
      if (!instanceA.correctType || !instanceB.correctType) return;
      if (instanceA.type === instanceB.type && instanceA.type !== 0) {
        instanceA.sameColorNeighbors.delete(instanceB);
        instanceB.sameColorNeighbors.delete(instanceA);
      }
    });
    world.on("beginContact", (evt) => {
      const bodyA: Body = evt.bodyA;
      const bodyB: Body = evt.bodyB;
      const instanceA = (bodyA as any).userData as CircleBlock;
      const instanceB = (bodyB as any).userData as CircleBlock;
      if (
        (instanceA.type === CircleBlock.goalType &&
          instanceB.type === CircleBlock.endType) ||
        (instanceB.type === CircleBlock.goalType &&
          instanceA.type === CircleBlock.endType)
      ) {
        this.levelComplete();
        return;
      }
      if (!instanceA.correctType || !instanceB.correctType) {
        let rocket = undefined;
        let block: CircleBlock = undefined;
        if (
          (instanceA as any).rocket &&
          instanceB.correctType &&
          instanceB.type !== CircleBlock.goalType
        ) {
          rocket = bodyA;
          block = instanceB;
        } else if (
          (instanceB as any).rocket &&
          instanceA.correctType &&
          instanceA.type !== CircleBlock.goalType
        ) {
          rocket = bodyB;
          block = instanceA;
        }
        if (rocket && block) {
          block.boom();
        }
      }
      if (!evt.shapeA.data || !evt.shapeB.data) return;
      if (instanceA.correctType && instanceB.correctType) {
        if (instanceA.type === instanceB.type && instanceA.type !== 0) {
          instanceA.sameColorNeighbors.set(instanceB, true);
          instanceB.sameColorNeighbors.set(instanceA, true);
        }
        let body = undefined;
        let inst = undefined;
        if (bodyA.type === Body.KINEMATIC && instanceA.type !== 0) {
          body = bodyA;
          inst = instanceA;
        } else if (bodyB.type === Body.KINEMATIC && instanceB.type !== 0) {
          body = bodyB;
          inst = instanceB;
        }
        if (body) {
          body.type = Body.DYNAMIC;
          if (instanceA.type === instanceB.type) {
            const steps = inst.steps;
            inst.animateAndDestroy();
            increaseBlocks.push({
              block: inst === instanceA ? instanceB : instanceA,
              steps,
            });
          } else {
            (stateManager.activeState as GameViewState).updateBoomMeter(1);
          }
        } else {
          if (
            instanceA.type === instanceB.type &&
            instanceA.type !== 0 &&
            (instanceA.steps !== instanceB.steps ||
              instanceA.kinematic ||
              instanceB.kinematic)
          ) {
            if (instanceA.kinematic) {
              const steps = instanceA.steps;
              instanceA.animateAndDestroy();
              increaseBlocks.push({ block: instanceB, steps });
            } else {
              const steps = instanceB.steps;
              instanceB.animateAndDestroy();
              increaseBlocks.push({ block: instanceA, steps });
            }
            
          }
        }
      }
    });
  }
  protected customLogic(): void {
    const big = unusedBodies.length >= 5;
    const small = unusedBodies.length === 2;
    const middle = unusedBodies.length >= 3;
    const amount = (small) ? 1 : (middle ? 2 : (big ? 3 : 0));
    const state = stateManager.activeState as GameViewState;
    if (!state || !state.updateBoomMeter) return; 
    if (amount > 0) {
      const body = unusedBodies[Math.floor(Math.random()*unusedBodies.length)];
      if (body) {
        const block = (body as any).userData as CircleBlock;
        state.fireMissle(amount, block.position);
      }
    }
    if (unusedBodies.length > 0) {
      state.updateBoomMeter((amount > 0) ? unusedBodies.length + 1 : 1);
    }
    let wasRainbow = false;
    unusedBodies.forEach((body) => {
      const block = (body as any).userData as CircleBlock;
      if (block && block.type === CircleBlock.rainbowType && block.steps > 3) {
        wasRainbow = true;
      }
      world.removeBody(body);
    });
    this.clearBodyArray();

    increaseBlocks.forEach((data) => data.block.increase(data.steps));
    increaseBlocks = [];

    explodePoints.forEach((pos) => {
      const bodies = world.hitTest(pos, world.bodies, 0);
      if (bodies && bodies.length > 0) {
        const block = (bodies[0] as any).userData as CircleBlock;
        if (block && block.correctType && block.type !== CircleBlock.goalType) {
          block.boom();
        }
      }
    });
    explodePoints = [];

    if (wasRainbow) {
      world.bodies.forEach((body)=> {
        const block = (body as any).userData as CircleBlock;
        if (block && block.correctType && block.correctType()) {
          state.createExplosion(block.position);
          block.boom();
        }
      });
      state.stopBonuses();
    }
  }
  protected levelComplete() {
    (stateManager.activeState as GameViewState).levelComplete();
  }
  protected async load() {
    return new Promise<void>((resolve, reject) => {
      sounds.init("assets/sounds");
      startLoading();
      const loader = new Loader();
      loader.add("base", "assets/atlases/base.json");

      //sounds.add("music", { autoplay: true, loop: true, volume: 0.6 });
      /*sounds.add("click", { volume: 1 });
      sounds.add("pop0", { volume: 0.6 });
      sounds.add("pop1", { volume: 0.6 });
      sounds.add("pop2", { volume: 0.6 });
      sounds.add("collide", { volume: 0.2 });
      sounds.add("boom", { volume: 1 });
      sounds.add("crab_crack", { volume: 1 });
      sounds.add("crab_hand", { volume: 1 });
      sounds.add("won", { volume: 0.4 });
      sounds.add("tutorial", { volume: 0.4 });
      sounds.add("level_failed", { volume: 1 });
      sounds.add("level_won", { volume: 1 });
      sounds.add("level", { volume: 0.4 });*/

      loader.onProgress.add((l) => {
        //loadingProgress(l.progress);
      });
      loader.onError.add((a, b, c) => {
        sendError({ error: `loading (${c.url})` });
        reject();
      });

      loader.load(async () => {
        await this.startAfterLoading();
        resolve();
      });
    });
  }
  private async startAfterLoading() {
    const preloader = document.getElementById("preloader");
    if (preloader) {
      preloader.style.display = "none";
    }
    /*if (gameSettings.allQuests) {
          const d = new Date();
          gameSettings.dateString = `${d.getFullYear()}${d.getMonth()+1}${d.getDate()}`;
          const level = gameSettings.allQuests.all.find((a)=>a.date === gameSettings.dateString);
          gameSettings.quest = level || undefined;
        }*/
    await finishLoading();
    //await leaderboards.load();
  }
}
