import gsap from "gsap";
import { Container, Sprite, Texture } from "pixi.js";

export class BoomMeter extends Container {
    private bar: Sprite;
    private max: number;
    private current: number;
    private initialHeight: number = 800;
    constructor() {
        super();
        const back = new Sprite(Texture.WHITE);
        back.tint = 0x333333;
        back.width = 32;
        back.height = this.initialHeight + 2;
        back.y = 1;
        this.addChild(back);
        back.anchor.set(0.5,1);

        this.bar = new Sprite(Texture.WHITE);
        this.bar.width = 30;
        this.bar.height = 1;
        this.addChild(this.bar);
        this.bar.anchor.set(0.5,1);
        this.current = 0;
        this.max = 10;
    }
    public set maximum(m: number) {
        this.max = m;
    }
    public update(amount: number): boolean {
        if (amount === 0) {
            this.current = 0;
        } else if (amount < 3) {
            amount = 0;
        } else {
            this.current += amount;
            this.current = Math.min(this.current, this.max);
        }
        const wasEnd = this.current >= this.max;
        gsap.killTweensOf(this.bar);
        gsap.to(this.bar, {height: this.initialHeight * (this.current / this.max), duration: 0.2});
        if (wasEnd) {
            this.current = 0;
            gsap.to(this.bar, {height: 0, delay: 0.2, duration: 0.2});
        }
        return wasEnd;
    }
}