import gsap, { Linear } from "gsap";
import { Container, Sprite, Texture } from "pixi.js";

export class NextBlocksTimer extends Container {
    private bar: Sprite;
    public isFrozen: boolean;
    private delay: gsap.core.Tween;
    constructor() {
        super();
        this.bar = new Sprite(Texture.WHITE);
        this.bar.width = 700;
        this.bar.height = 50;
        this.addChild(this.bar);
        this.bar.anchor.set(0.5,0.5);
    }
    public setProgress(p: number) {
        this.bar.width = 700*p;
    }
    public freeze(seconds: number) {
        this.isFrozen = true;
        if (this.delay) {
            this.delay.kill();
        }
        this.delay = gsap.delayedCall(seconds, ()=> {
            this.isFrozen = false;
        });
    }
    public clear() {
        if (this.delay) {
            this.delay.kill();
            this.delay = undefined;
        }
        this.isFrozen = false;
    }
}