import gsap from "gsap";
import { Container, Sprite, Text, Texture } from "pixi.js";
export enum EBonus {
    None = 0,
    Rainbow,
    Kill,
    Freeze,
    All
}
export class BonusChooser extends Container {
    private txt: Text;
    public bonus: EBonus;
    constructor() {
        super();
        const back = new Sprite(Texture.WHITE);
        back.anchor.set(0.5, 0.5);
        back.tint = 0x111111;
        back.width = 150;
        back.height = 50;
        this.addChild(back);

        this.txt = new Text("...",{fontSize: 25, fill: 0xFFFFFF, align: "center"});
        this.txt.anchor.set(0.5, 0.5);
        this.addChild(this.txt);
        //this.hide();
    }
    public async show(bonus: EBonus) {
        this.bonus = bonus;
        this.visible = true;
        let n = "";
        switch(bonus) {
            case EBonus.Rainbow:
                n = "Rainbow";
                break;
            case EBonus.Freeze:
                n = "Freezer";
                break;
            case EBonus.Kill:
                n = "Killer";
                break;
        }
        this.txt.text = n;
        this.delay(2).then(()=> {
            this.txt.text = "...";
        });
        await this.delay(2);
        //this.hide();
    }
    public hide() {
        this.visible = false;
    }
    private async delay (t: number) {
        return new Promise<void>((resolve)=> {
            gsap.delayedCall(t, ()=> {
                resolve();
            })
        });
    }
}